import emitter from '@/methods/emitter';

export default function (response, title = '更新'){
    if(response.data.success){
        emitter.emit('push-message', {
            style: 'success',
            title: `${title}成功`,
        });
    }else {
        const message = typeof response.data.success === 'string' ? [response.data.success] : response.data.success;
        emitter.emit('push-message', {
            style: 'danger',
            title: `${title}失敗`,
            content: message.join('、'),
        })
    }
}